<template>
  <div>
    <van-nav-bar
      :fixed="true"
      title="新增"
      right-text="完成"
      @click-left="()=>{
          $router.go(-1)
         }"
      @click-right="submit()"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="plus" slot="right" size="20" /> -->
    </van-nav-bar>
    <!-- <div class="titleHistoryContainer backgroundNaviBar headerCont">
      <div class="imageSizeHistory" @click="()=>{
            $router.go(-1)}">
        <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
      </div>
      <div class="titleHistory">
        <span class="position-relative">新增</span>
      </div>
      <div slot="right" class="icons" size="20">
        <div style="background:#fff;height:24px;" class="rightContent" @click="submit()">
          <span style="font-size:14px;color:#43aefd">完成</span>
        </div>
      </div>
    </div>-->
    <!-- </div> -->
    <div style="margin-top:50px;">
      <div class="boxFlexCont contPadding border">
        <div style="flex:2">
          <span>时间：</span>
        </div>
        <div class="boxFlexCont justyBetween" style="flex:5" @click="selectTime()">
          <div>
            <input class="input" type="text" v-model="generationTime" disabled placeholder="选择转运时间" />
          </div>
          <div class="imageSize">
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
      </div>
      <div class="boxFlexCont contPadding border">
        <div style="flex:2">
          <span>废液站点：</span>
        </div>
        <div
          class="boxFlexCont justyBetween"
          style="flex:5"
          @click="()=>{
           showStation= true;
          }"
        >
          <div>
            <input
              class="input"
              type="text"
              placeholder="选择产生废液转运站点"
              disabled
              v-model="generateSite"
            />
          </div>
          <div class="imageSize">
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
      </div>
      <div class="boxFlexCont contPadding border">
        <div style="flex:2">
          <span>负责人：</span>
        </div>
        <div class="boxFlexCont justyBetween" style="flex:5">
          <div>
            <input class="input" type="text" placeholder="请输入负责人" v-model="responsiblePerson" />
          </div>
          <div class="imageSize">
            <!-- <img src="static/img/toBottom.png" alt /> -->
          </div>
        </div>
      </div>
      <div class="boxFlexCont contPadding border">
        <div style="flex:2">
          <span>产生量：</span>
        </div>
        <div class="boxFlexCont" style="flex:5">
          <div>
            <!-- <input class="input" type="text" disabled placeholder="负责人" /> -->
            <van-stepper v-model="wasteNamber" integer />
          </div>
          <div style="font-size:14px;color:#ccc;margin-left:10px;">
            <!-- <img src alt /> -->
            <span>桶</span>
          </div>
        </div>
      </div>
      <div class="boxFlexCont contPadding border">
        <div style="flex:2">
          <span>联系方式：</span>
        </div>
        <div class="boxFlexCont" style="flex:5">
          <div>
            <input maxlength="11" class="input" type="text" placeholder="输入联系方式" v-model="phone" />
          </div>
          <div>
            <img src alt />
          </div>
        </div>
      </div>
      <div class="boxFlexCont contPadding border">
        <div style="flex:2">
          <span>目的地：</span>
        </div>
        <div
          class="boxFlexCont justyBetween"
          style="flex:5"
          @click="()=>{
          showdestination = true;
          }"
        >
          <div style="display:flex;flex:1">
            <input
              style="flex:1"
              class="input"
              type="text"
              placeholder="选择转运目的地"
              disabled
              v-model="destination"
            />
          </div>
          <div class="imageSize">
            <!-- <img src alt /> -->
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
      </div>
      <div class="boxFlexCont contPadding">
        <div>
          <span>图片：</span>
        </div>
      </div>
      <div class="contPadding background">
        <van-uploader v-model="fileList" multiple :after-read="afterRead" />
      </div>
    </div>
    <van-calendar
      v-model="showTime"
      :show-confirm="false"
      color="#328efb"
      :default-date="defaultTime"
      :min-date="minDate"
      :safe-area-inset-bottom="true"
      @confirm="confirmTime"
    />
    <van-popup v-model="showStation" position="bottom" style="z-index:999">
      <van-picker
        :columns="columns"
        show-toolbar
        :close-on-click-overlay="false"
        title="选择站点"
        :default-index="0"
        @cancel="()=>{
          showStation = false;
        }"
        @confirm="onChange"
      />
    </van-popup>
    <van-popup v-model="showdestination" position="bottom" style="z-index:999">
      <van-picker
        :columns="destinationArr"
        show-toolbar
        :close-on-click-overlay="false"
        title="转运目的地"
        :default-index="0"
        @cancel="()=>{
          showdestination = false;
        }"
        @confirm="onChangedestination"
      />
    </van-popup>

    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        @confirm="confirmTime"
      />
    </van-popup>
    <!-- <van-overlay :show="loadingShow">
      <div class="wrapper" @click.stop>
        <van-loading type="spinner" />
      </div>
    </van-overlay>-->
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
import Vue from "vue";
import { Overlay } from "vant";
// import { Loading } from "vant";
Vue.use(Overlay);
import { Dialog, Toast } from "vant";
import { Stepper } from "vant";
import { Uploader } from "vant";
import AppBace from "../../../static/ajax";
import { Calendar } from "vant";
import { Loading } from "vant";
Vue.use(Loading);
Vue.use(Calendar);
Vue.use(Uploader);
Vue.use(Stepper);
export default {
  data() {
    return {
      wasteNamber: 0,
      formdata: new FormData(),
      fileList: [
        // { url: "https://img.yzcdn.cn/vant/leaf.jpg" }
        // Uploader 根据文件后缀来判断是否为图片文件
        // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
        // { url: "https://cloud-image", isImage: true }
      ],
      token: "",
      // 转运时间
      generationTime: "",
      // 地点
      generateSite: "",
      // 负责人
      responsiblePerson: "",
      // 联系方式
      phone: "",
      // 目的地
      destination: "",
      showTime: false,
      minDate: new Date(2010, 0, 1),
      defaultTime: new Date(),
      columns: this.$store.state.menu,
      showStation: false,
      // minDate: new Date(2020, 0, 1),
      // maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      show: false,
      loadingShow: false,
      showdestination: false,
      destinationArr: [],
      showover: false
    };
  },
  async created() {
    this.token = sessionStorage.getItem("token");
    var url = "/wastetransfer/wastetransfer/destination";
    var res = await AppBace.request(url, "GET", {}, this.token);
    console.log(res);
    this.destinationArr = res.data.data;
  },
  mounted() {
    // this.token = sessionStorage.getItem("token");
    // console.log(localStorage.getItem('destination'));
    // this.destinationArr = localStorage.getItem('destination').split(',');
    // this.phone = sessionStorage.getItem("phone");
    // this.responsiblePerson = sessionStorage.getItem("username");
  },
  methods: {
    confirmTime(val) {
      // console.log(val);
      this.generationTime = AppBace.filterTime(val);
      this.show = false;
      // console.log(this.generationTime);
    },
    // 选择站点
    onChange(val) {
      this.generateSite = val;
      this.showStation = false;
    },
    onChangedestination(val) {
      this.destination = val;
      this.showdestination = false;
    },
    selectTime() {
      this.show = true;
    },
    confirm() {
      this.show = false;
    },
    afterRead(files) {
      console.log("files", files);
      this.formdata.append("files", files.file);
    },
    submit() {
      var _this = this;
      Dialog.confirm({
        title: "新增",
        message: "确认提交表格？"
      })
        .then(() => {
          // on confirm
          // _this.loadingShow = true;
          // Toast.loading({
          //   message: "加载中...",
          //   forbidClick: true,
          //   loadingType: "spinner"
          // });
          _this.showover = true;
          _this.submitConfirm();
        })
        .catch(() => {
          // on cancel
          Toast.fail("已取消");
        });
    },
    async submitConfirm() {
      // console.log("1");
      var url = "/wastetransfer/wastetransfer/insert";
      // var starttime = startTime;
      // var lasttime = endTime;
      // var isExcel = "no";
      // var pageNum = 1;
      // var pageSize = 10;
      // var waterstation = station;
      // var day = 0;
      // var advice = "";
      // day = AppBace.dateTimeDev(starttime, lasttime);
      // // day = AppBace.dateTimeDev(starttime, lasttime);
      // var res = await AppBace.request(
      //   url,
      //   "POST",
      //   {
      //     starttime,
      //     lasttime,
      //     waterstation,
      //     pageNum,
      //     pageSize,
      //     isExcel
      //   },
      //   this.token
      // );
      // console.log(res);
      // 数量

      if (
        this.wasteNamber == 0 ||
        this.destination == "" ||
        this.generateSite == "" ||
        this.phone == "" ||
        this.responsiblePerson == ""
      ) {
        Toast.fail("请完善信息");
        this.showover = false;
      } else {
        this.formdata.append("amount", this.wasteNamber);
        // 目的地
        this.formdata.append("destination", this.destination);
        // 废液产生站点
        this.formdata.append("generateSite", this.generateSite);
        // 产生时间
        this.formdata.append("generationTime", this.generationTime);
        // 联系方式
        this.formdata.append("phone", this.phone.trim());
        // 照片
        // this.formdata.append("photo", this.photo);
        // 转运负责人
        this.formdata.append("responsiblePerson", this.responsiblePerson);

        // this.formdata.append("destination", _this.description);

        console.log(this.formdata);
        var res = await AppBace.request(url, "POST", this.formdata, this.token);

        console.log(res);

        if (res.status === 200) {
          if (res.data.message === "success") {
            this.$router.go(-1);
            // Toast(res.data.info);
            Toast.success(res.data.info);
            this.showover = true;
          } else {
            Toast(res.data.info);
          }
        } else {
          // Toast("请求超时");
          Toast.fail("请求超时");
          this.showover = true;
        }
      }
    }
  }
};
</script>
<style scoped>
.rowheader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.leftHeader {
  flex: 1;
  display: flex;
  justify-content: center;
}
.rightHeader {
  flex: 1;
  display: flex;
  justify-content: center;
  font-size: 12px;
  align-items: center;
  color: rgb(148, 185, 253);
}
.centerHeader {
  flex: 4;
  display: flex;
  justify-content: center;
}
.rightContent {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  border-radius: 2px;
}
.headerCont {
  position: fixed;
  top: 0;
  width: 100%;
}
.input {
  border: none;
  background: white;
  flex: 1;
}
.boxFlexCont {
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 14px;
  /* justify-content: center; */
}
.contPadding {
  padding: 10px 20px;
  background-color: white;
}
.border {
  border-bottom: 1px rgb(240, 240, 240) solid;
}
.background {
  background-color: white;
}
.imageSize img {
  width: 20px;
  height: 20px;
}
.justyBetween {
  display: flex;
  justify-content: space-between;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
.titleHistoryContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 0 10px;
  /* background-color: #3572fe; */
  color: white;
  font-size: 18px;
}
</style>


